// CP_PATH, BDA_PATH
import {
  CP_PATH,
  BDA_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 停电敏感-获取供电单位编码
 * @const
 * @type {String}
 * @default  '[CP_PATH]/residentialElectricity/getPowerAreaCode'
 */
export const GET_POWER_AREA_CODE = CP_PATH + '/residentialElectricity/getPowerAreaCode'

/**
 * 停电敏感-获取供电单位编码
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getPowerAreaCode(data) {
  return ajax({
    method: 'post',
    headers: {
      "Content-Type": "application/json;charset=utf8"
    },
    data: data,
    url: GET_POWER_AREA_CODE
  })
}

