<template>
  <div class="panoramicAnalysis">
    <div class="header">
      <el-select
        size="small"
        class="input"
        v-model="queryForm.dsj"
        clearable
        placeholder="请选择地市局"
        @change="dsjChange"
      >
        <el-option
          v-for="item in dsjOptions"
          :key="item.ORG_CODE"
          :label="item.ORG_NAME"
          :value="item.ORG_CODE"
        ></el-option>
      </el-select>
      <el-select
        v-model="queryForm.xqj"
        clearable
        placeholder="请选择县区局"
        class="input"
        size="small"
        @change="qxjChange"
      >
        <el-option
          v-for="item in xqjOptions"
          :key="item.ORG_CODE"
          :label="item.ORG_NAME"
          :value="item.ORG_CODE"
        ></el-option>
      </el-select>
      <el-select size="small" class="input" v-model="queryForm.gds" clearable placeholder="请选择供电所">
        <el-option
          v-for="item in gdsOptions"
          :key="item.ORG_CODE"
          :label="item.ORG_NAME"
          :value="item.ORG_CODE"
        ></el-option>
      </el-select>
      <el-date-picker
        size="small"
        class="input"
        v-model="queryForm.time"
        type="date"
        value-format="yyyy-MM-dd"
        placeholder="请选择时间"
      ></el-date-picker>
      <el-button type="primary" size="small" icon="el-icon-search" @click="getHotWordData">查询</el-button>
      <el-button type="primary" size="small" icon="el-icon-search" @click="getExport">导出</el-button>
    </div>
    <div class="top content-border">
      <div class="content">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span>工单热点分布</span>
          </div>
        </div>
        <div class="chart-main" id="randomCircle">
          <wordCloud
            v-if="hotWordRes"
            :wordCloudData="hotWordData"
            :name="'工单热点分布'"
            @setOption="setOption"
          ></wordCloud>
        </div>
      </div>
    </div>

    <div class="bottom">
      <div class="content">
        <!-- <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span class="text">客户关注热点趋势分析</span>
          </div>
        </div>-->
        <el-table
          class="content-table"
          border
          :height="350"
          :data="tableData"
          :row-class-name="tableRowClassName"
        >
          <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
          <el-table-column prop="gddwbqmc" label="供电单位标签名称"></el-table-column>
          <el-table-column prop="hdmc" label="推荐营销活动名称"></el-table-column>
          <el-table-column prop="hdyxq" label="营销活动有效期"></el-table-column>
          <el-table-column prop="updateTime" label="数据更新时间"></el-table-column>
          <el-table-column align="center" width="200" label="选择营销活动">
            <template slot-scope="scope">
              <!-- <span class="text" type="text">推荐</span> -->
              <el-checkbox
                v-model="scope.row.flagB"
                :disabled="scope.row.flagDisabled"
                @change="flagBChange(scope.row)"
              ></el-checkbox>
            </template>
          </el-table-column>
        </el-table>
        <div class="content-page">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="this.queryForm.current"
            :page-sizes="[10,20,30,40]"
            :page-size="this.queryForm.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.queryForm.total"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface1 from '@/base/mixin/precision-marketing';
import Interface2 from '@/base/mixin/common-api';
import wordCloud from '@/components/word-cloud/word-cloud'
import { CP_PATH } from '@/config'

export default {
  components: {

    wordCloud,
  },
  name: "panoramicAnalysis",
  mixins: [Interface1, Interface2],
  data() {
    return {
      dsjOptions: [],
      xqjOptions: [],
      gdsOptions: [],
      queryForm: {

        dsj: "",
        xqj: "",
        gds: "",
        time: "",
        current: 1,
        size: 10,
        total: 0,
      },
      //工单热点分布
      hotWordData: {},
      hotWordRes: false,
      // 营销表格
      tableData: [],
      //图表组件数据
      setOptionData: {},
    }
  },
  mounted() {
    this.getAreaCode()
    this.getTableData()
    window.addEventListener("resize", () => {
      if (this.setOptionData['工单热点分布']) this.setOptionData['工单热点分布'].resize()
    });

    //热词分布默认值
    this.hotPointDistribution({
      // gddwbm: "03061028",
      gddwbm: '03081032',
      rq: '2021-06-18'
    }).then(res => {
      console.log(res, 'hotWordDistribution')
      if (res) {
        this.hotWordData = res.map(e => {
          return {
            name: e.rdmc,
            valueShow: e.sql,
          }
        })
        this.hotWordRes = true
      }
    })
  },
  methods: {
    //导出热词分布
    getExport(){
      window.location.href = CP_PATH + '/gddwztxx/exportHotPointDistribution?gddwbm=' + this.queryForm.gds+'&rq='+ this.queryForm.time
    },
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val
    },
    //表格样式
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    //地市局选择
    dsjChange() {
      this.queryForm.gds = ''
      this.gdsOptions = []
      this.queryForm.xqj = ''
      this.xqjOptions = []
      this.getQxjPowerAreaCode()
    },
    //区县局选择
    qxjChange() {
      this.queryForm.gdj = ''
      this.gdsOptions = []
      this.getGdsPowerAreaCode()
    },
    // 分页
    handleSizeChange(val) {
      this.queryForm.size = val
      this.getTableData()
    },
    handleCurrentChange(val) {
      this.queryForm.current = val
      this.getTableData()
    },
    //选择营销活动
    flagBChange(row) {
      this.$confirm('是否新启用销活动?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let tableData = [...this.tableData]
        this.tableData = []
        this.tableData = [...tableData]
        this.addActivityConfig({
          activityId: row.activityId,
          activityName: row.hdmc,
        }).then(res => {
          this.$message.success('启用营销活动成功')
          this.getTableData()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //获取供电单位编码--地市局
    getAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
        // "orgCode":"0306" //上一级编码
      }).then(res => {
        this.dsjOptions = res
      })
    },
    //获取区县局--区县局
    getQxjPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.queryForm.dsj //上一级编码
      }).then(res => {
        this.xqjOptions = res
      })
    },
    //获取供电所
    getGdsPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.queryForm.xqj //上一级编码
      }).then(res => {
        this.gdsOptions = res
      })
    },
    //获取热词
    getHotWordData() {
      this.hotWordRes = false
      if (!this.queryForm.gds) {
        this.$message.warning('请选择供电所')
        return
      }
      this.hotPointDistribution({
        // gddwbm: "03061028",
        gddwbm: this.queryForm.gds,
        rq: this.queryForm.time
      }).then(res => {
        console.log(res, 'hotWordDistribution')
        if (res) {
          this.hotWordData = res.map(e => {
            return {
              name: e.rdmc,
              valueShow: e.sql,
            }
          })
          this.hotWordRes = true
        }
      })
    },
    //
    getTableData() {
      this.tableData = []
      this.powerTagAndActivityList(this.queryForm).then(res => {
        this.tableData = [...res.records]
        this.tableData.forEach(e => {
          e.flagB = e.flag === '1' ? true : false
          e.flagDisabled = e.flag === '1' ? true : false
        })
        this.queryForm.total = res.total
      })
    }
  },
}
</script>

<style scoped lang="scss">
::v-deep .el-table .warning-row {
  // background: #e7f6f7;
  background: #e9f3fe;
}
.panoramicAnalysis {
  width: 100%;
  height: 100%;
  font-size: 14px;
  box-sizing: border-box;
  .content-border {
    background: #fff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .header {
    height: 40px;
    display: flex;
    align-items: center;
    .input {
      margin-right: 20px;
    }
  }
  .top {
    width: 100%;
    // height: calc(50% - 10px);
    box-sizing: border-box;
    margin-bottom: 20px;
    height: 400px;
    .content {
      width: 100%;
      height: calc(100% - 40px);
      display: flex;
      flex-wrap: wrap;
      .content-title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 30px;
        line-height: 30px;
        padding: 5px 0px 0px 5px;
        font-weight: bold;
        color: #1a84f4;
        .block {
          display: inline-block;
          width: 8px;
          height: 16px;
          margin: 0 10px;
          background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
        }
        .content-title-right {
          margin-right: 20px;
        }
      }
      .chart-main {
        width: 100%;
        height: calc(100% - 30px);
      }
    }
  }
  .bottom {
    ::v-deep .el-table {
      .success-row {
        background: rgb(244, 244, 244);
      }
      .cell {
        padding: 2px 4px;
      }
      th {
        background-color: rgb(24, 153, 250);
        .cell {
          color: #ffffff;
        }
      }
    }
    .content-table {
      width: 100%;
      // height: 350px;
      .text {
        color: #1899fa;
        cursor: pointer;
      }
    }
    .content-page {
      margin-top: 10px;
      width: 100%;
      text-align: center;
    }
  }
}
</style>