/**
 *  公共接口-多次用到 mixin module
 *  @module base/mixin/commonApi
 *  @author coder
 */
import {
  getPowerAreaCode,
  getBdaTagList,
  getXl
} from '@/base/api/common-api'

// mixin
export default {

  /**
   * 公共接口-多次用到数据存储载体对象
   * @member commonApi
   * @property {Array} [list=[]] 获取公共接口-多次用到列表响应数据
   * @property {number} [page=null] 获取公共接口-多次用到列表响应页码
   * @property {number} [limit=null] 获取公共接口-多次用到列表页大小，每页记录条数
   * @property {number} [total=0] 获取公共接口-多次用到列表数据记录总条数
   * @property {object} [model=null] 获取公共接口-多次用到单条记录
   */
  data() {
    return {
      commonApi: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 停电敏感-获取供电单位编码
     * @function getPowerAreaCode
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getPowerAreaCode(data) {
      return getPowerAreaCode(data).then(res => {
        return res
      })
    },
    /**
     * 主题标签-技能配置-标签树
     * @function getBdaTagList
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getBdaTagList(data) {
      return getBdaTagList(data).then(res => {
        return res
      })
    },
    /**
     * 产品标签列表平滑list
     * @function getXl
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getXl(data) {
      return getXl(data).then(res => {
        return res
      })
    }
  }
}
